/**************************************************************************
 * IMPORTS
 ***************************************************************************/

import BannerLayout from "@/components/widgets/layouts/BannerLayout"
import PopoverLayout from "@/components/widgets/layouts/PopoverLayout"
import PopupLayout from "@/components/widgets/layouts/PopupLayout"

/**************************************************************************
 * MIXINS > LAYOUT
 * @docs https://vuejs.org/v2/guide/mixins.html
 ***************************************************************************/

export default {
  components: {
    BannerLayout,
    PopoverLayout,
    PopupLayout
  },

  props: {
    layout: {
      type: String,
      required: true,
      validator(x) {
        return ["banner", "popover", "popup"].includes(x)
      }
    }
  }
}
