<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
transition(
  :appear="true"
  :enter-active-class="'u-animated' + ' ' + (transition ? 'u-ms500' : 'u-ms0') + ' ' + 'u-fade-in-' + position.split('-')[1]"
  :leave-active-class="'u-animated' + ' ' + (transition ? 'u-ms500' : 'u-ms0') + ' ' + 'u-fade-out-' + position.split('-')[1]"
)
  div(
    v-if="isTriggered && isClosed === false"
    :class=`[
      "c-popover-layout",
      "c-popover-layout--" + direction,
      "c-popover-layout--" + position,
      "c-popover-layout--" + size,
      "c-popover-layout--" + theme,
      "c-popover-layout--popover-size-" + popoverSize,
      "u-no-drag",
      "u-reset-widget",
      "js-editor-transform",
      {
        "c-popover-layout--editor": editor
      }
    ]`
  )
    div(
      class="c-popover-layout__widget"
      key="widget"
    )
      widget-close(
        @close="onEmitClose"
        class="c-popover-layout__close"
        theme="light"
      )

      .c-popover-layout__box
        slot(
          name="header"
        )

        slot(
          name="body"
        )

        widget-branding(
          v-if="hasWidgetBranding"
          :theme="theme"
        )
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// PROJECT: COMPONENTS
import WidgetBranding from "@/components/widgets/common/WidgetBranding"
import WidgetClose from "@/components/widgets/common/WidgetClose"

// PROJECT: MIXINS
import CloseMixin from "@/mixins/main/CloseMixin"
import DirectionMixin from "@/mixins/main/DirectionMixin"
import EditorMixin from "@/mixins/main/EditorMixin"
import PositionMixin from "@/mixins/main/PositionMixin"
import SubscriptionMixin from "@/mixins/main/SubscriptionMixin"
import SizeMixin from "@/mixins/main/SizeMixin"
import ThemeMixin from "@/mixins/main/ThemeMixin"
import TransitionMixin from "@/mixins/main/TransitionMixin"
import TriggerMixin from "@/mixins/main/TriggerMixin"

export default {
  components: {
    WidgetBranding,
    WidgetClose
  },

  mixins: [
    CloseMixin,
    DirectionMixin,
    EditorMixin,
    PositionMixin,
    SizeMixin,
    SubscriptionMixin,
    ThemeMixin,
    TransitionMixin,
    TriggerMixin
  ],

  props: {
    popoverSize: {
      type: String,
      default: "default",
      validator(x) {
        return ["mini", "small", "default", "medium", "large"].includes(x)
      }
    }
  }
}
</script>

<!-- *************************************************************************
     STYLE
     ************************************************************************* -->

<style lang="scss" scoped>
// VARIABLES
$c: ".c-popover-layout";
$sizes: "mini", "small", "default", "medium", "large";
$popoverSizes: "mini", "small", "default", "medium", "large";

#{$c} {
  position: fixed;
  box-sizing: border-box;
  padding: 10px;
  width: 100%;

  #{$c}__widget {
    #{$c}__box {
      overflow: hidden;
      border-width: 1px;
      border-style: solid;
      border-radius: 6px;
    }
  }

  // --> POSITIONS <--

  &--bottom-left {
    bottom: 0;
    left: 0;

    #{$c}__widget {
      #{$c}__close {
        top: -17px;
        left: 10px;
      }
    }
  }

  &--bottom-right {
    right: 0;
    bottom: 0;

    #{$c}__widget {
      #{$c}__close {
        top: -17px;
        right: 10px;
      }
    }
  }

  &--top-left {
    top: 0;
    left: 0;

    #{$c}__widget {
      #{$c}__close {
        bottom: -17px;
        left: 10px;
      }
    }
  }

  &--top-right {
    top: 0;
    right: 0;

    #{$c}__widget {
      #{$c}__close {
        right: 10px;
        bottom: -17px;
      }
    }
  }

  // --> BOOLEANS <--

  &--editor {
    position: absolute;
  }

  // --> THEMES <--

  @each $theme in $themes {
    $themeName: map-get($theme, "name");

    &--#{$themeName} {
      #{$c}__widget {
        #{$c}__box {
          border-color: mdg($theme, "borders", "default", "primary");
          background-color: mdg($theme, "backgrounds", "default", "primary");
          color: mdg($theme, "fonts", "default", "primary");
        }
      }
    }
  }
}

// --> BREAKPOINT: MOBILE WIDE <--

@include mq($from: mobileWide) {
  #{$c} {
    // --> SIZES <--

    @each $popoverSize in $popoverSizes {
      $i: index($popoverSizes, $popoverSize) - 1;

      &--popover-size-#{$popoverSize} {
        @each $size in $sizes {
          $j: index($sizes, $size) - 1;

          &#{$c}--#{$size} {
            &#{$c}--horizontal {
              width: 350px + (25px * $i) + (50px * $j);
            }

            &#{$c}--vertical {
              width: 250px + (25px * $i) + (25px * $j);
            }
          }
        }
      }
    }
  }
}
</style>
