<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
div(
  :class=`[
    "c-widget-header",
    "c-widget-header--" + color,
    "c-widget-header--" + size,
    "c-widget-header--" + theme
  ]`
)
  slot(
    name="default"
  )
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// PROJECT: MIXINS
import SizeMixin from "@/mixins/main/SizeMixin"
import ThemeMixin from "@/mixins/main/ThemeMixin"

export default {
  mixins: [SizeMixin, ThemeMixin],

  props: {
    color: {
      type: String,
      default: "dark",
      validator(x) {
        return ["dark", "darker"].includes(x)
      }
    }
  }
}
</script>

<!-- *************************************************************************
     STYLE
     ************************************************************************* -->

<style lang="scss" scoped>
// VARIABLES
$c: ".c-widget-header";
$sizes: "mini", "small", "default", "medium", "large";

#{$c} {
  border-bottom-width: 1px;
  border-bottom-style: solid;
  text-align: center;
  font-weight: 600;

  // --> SIZES <--

  @each $size in $sizes {
    $i: index($sizes, $size) - 1;

    &--#{$size} {
      padding: (8px + (1px * $i));
      font-size: 12px + (1px * $i);
      line-height: 18px + (1px * $i);
    }
  }

  // --> THEMES <--

  @each $theme in $themes {
    $themeName: map-get($theme, "name");

    &--#{$themeName} {
      border-bottom-color: mdg($theme, "borders", "default", "primary");
      box-shadow: 0 1px 3px mdg($theme, "shadows", "default", "primary");

      &#{$c}--dark {
        background-color: mdg($theme, "backgrounds", "default", "secondary");
      }

      &#{$c}--darker {
        background-color: mdg($theme, "backgrounds", "default", "tertiary");
      }
    }
  }
}
</style>
