<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
component(
  v-bind="$props"
  @close="onClose"
  :class=`[
    "c-youtube-video",
    "c-youtube-video--" + computedDirection,
    "c-youtube-video--" + layout,
    "c-youtube-video--" + size,
    "c-youtube-video--" + theme,
    {
      "c-youtube-video--reverse": reverse
    }
  ]`
  :direction="computedDirection"
  :is="layout + '-layout'"
  :is-closed="closed"
  :is-triggered="triggered"
  :transition="transition"
)
  widget-header(
    v-if="titleContent"
    :size="size"
    :theme="theme"
    slot="header"
  ) {{ titleContent }}

  div(
    class="c-youtube-video__body"
    slot="body"
  )
    iframe(
      :src="youtubeLink"
      allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
      class="c-youtube-video__video"
      frameborder="0"
      loading="lazy"
      allowfullscreen
    )

    .c-youtube-video__content
      base-button(
        @click="onSeeProduct"
        :color="buttonColor"
        :full-width="true"
        :left-icon="buttonLeftIcon"
        :size="size"
        :theme="theme"
      ) {{ buttonContent }}

      p(
        v-if="descriptionContent"
        v-html="descriptionContent"
        class="c-youtube-video__description"
      )

      a(
        v-if="channelContent"
        :href="channelLink"
        class="c-youtube-video__channel"
        target="_blank"
      )
        youtube-logo(
          class="c-youtube-video__youtube-logo"
        )

        span.c-youtube-video__channel-content {{ channelContent }}
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// NPM
import { BaseButton } from "@growthbunker/vuedarkmode"

// PROJECT: COMPONENTS
import WidgetHeader from "@/components/widgets/common/WidgetHeader"
import YoutubeLogo from "@/components/widgets/logos/YoutubeLogo"

// PROJECT: MIXINS
import CloseMixin from "@/mixins/main/CloseMixin"
import DirectionMixin from "@/mixins/main/DirectionMixin"
import EditorMixin from "@/mixins/main/EditorMixin"
import LayoutMixin from "@/mixins/main/LayoutMixin"
import PositionMixin from "@/mixins/main/PositionMixin"
import ReverseMixin from "@/mixins/main/ReverseMixin"
import SizeMixin from "@/mixins/main/SizeMixin"
import SubscriptionMixin from "@/mixins/main/SubscriptionMixin"
import ThemeMixin from "@/mixins/main/ThemeMixin"
import TransitionMixin from "@/mixins/main/TransitionMixin"
import TriggerMixin from "@/mixins/main/TriggerMixin"
import WidgetMixin from "@/mixins/main/WidgetMixin"

export default {
  name: "YoutubeVideo",

  components: {
    WidgetHeader,
    BaseButton,
    YoutubeLogo
  },

  mixins: [
    CloseMixin,
    DirectionMixin,
    EditorMixin,
    LayoutMixin,
    PositionMixin,
    ReverseMixin,
    SizeMixin,
    SubscriptionMixin,
    ThemeMixin,
    TransitionMixin,
    TriggerMixin,
    WidgetMixin
  ],

  props: {
    buttonColor: {
      type: String,
      default: "blue",
      validator(x) {
        return [
          "black",
          "blue",
          "green",
          "grey",
          "orange",
          "purple",
          "red",
          "turquoise",
          "white",
          "yellow"
        ].includes(x)
      }
    },
    buttonContent: {
      type: String,
      required: true
    },
    buttonLeftIcon: {
      type: String,
      default: null
    },
    buttonLink: {
      type: String,
      required: true
    },
    buttonNewTab: {
      type: Boolean,
      default: true
    },
    channelContent: {
      type: String,
      default: null
    },
    channelLink: {
      type: String,
      default: null
    },
    descriptionContent: {
      type: String,
      default: null
    },
    titleContent: {
      type: String,
      default: null
    },
    videoAutoplay: {
      type: Boolean,
      default: true
    },
    videoId: {
      type: String,
      required: true
    }
  },

  computed: {
    computedDirection() {
      return this.layout === "banner" ? "horizontal" : this.direction
    },
    youtubeLink() {
      const autoplay = this.videoAutoplay ? "1" : "0"

      return `https://www.youtube.com/embed/${this.videoId}?autoplay=${autoplay}&rel=0`
    }
  },

  methods: {
    // --> EVENT LISTENERS <--

    onSeeProduct() {
      const target = this.buttonNewTab ? "_blank" : "_self"

      window.open(this.buttonLink, target)
    }
  }
}
</script>

<!-- *************************************************************************
     STYLE
     ************************************************************************* -->

<style lang="scss" scoped>
