/**************************************************************************
 * MIXINS > POSITION POPOVER
 * @docs https://vuejs.org/v2/guide/mixins.html
 ***************************************************************************/

export default {
  props: {
    position: {
      type: String,
      required: true,
      validator(x) {
        return [
          "bottom",
          "bottom-left",
          "bottom-right",
          "center",
          "top",
          "top-left",
          "top-right"
        ].includes(x)
      }
    }
  }
}
