/**************************************************************************
 * MIXINS > CLOSE
 * @docs https://vuejs.org/v2/guide/mixins.html
 ***************************************************************************/

export default {
  props: {
    isClosed: {
      type: Boolean,
      default: false
    }
  },

  data: () => ({
    // --> STATE <--

    closed: null
  }),

  mounted() {
    if (!this.editor) {
      // Hide widget when it has already been closed
      this.closed = !!localStorage.getItem(`${this.widgetId}-closed`)
    } else {
      this.closed = false
    }
  },

  methods: {
    // --> EVENT LISTENERS <--

    onClose() {
      // Prevent closing on editor mode
      if (!this.editor) {
        this.closed = true

        localStorage.setItem(`${this.widgetId}-closed`, true)
      } else {
        this.$emit("closeForbidden")
      }
    },

    onEmitClose() {
      this.$emit("close")
    }
  }
}
