/**************************************************************************
 * MIXINS > SUBSCRIPTION
 * @docs https://vuejs.org/v2/guide/mixins.html
 ***************************************************************************/

export default {
  props: {
    branding: {
      type: Boolean,
      default: false
    },
    subscribed: {
      type: Boolean,
      default: false
    }
  },

  computed: {
    hasWidgetBranding() {
      // When not subscribed always display branding
      if (this.subscribed === false) {
        return true
      } else {
        return this.branding
      }
    }
  }
}
